@import url("https://fonts.googleapis.com/css?family=Oxygen|Source+Sans+Pro:900");
@import url("https://unpkg.com/basscss@8.0.2/css/basscss.min.css");

html,
body {
  font-family: "Oxygen", sans-serif;
  line-height: 1.5;
  color: #566b78;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

a {
  color: #2980ba;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Source Sans Pro", sans-serif;
  margin-top: 1em;
  padding-top: 1em;
  margin-bottom: 0em;
  color: #333;
}

em,
b,
strong {
  color: #333;
}

.app-body {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
  margin-bottom: 10rem;
}

header {
  height: 300px;
  overflow: auto;
  background-color: #263d36;
  background-image: url("assets/header.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: flex-end;
}

.header-content {
  width: 800px;
  max-width: 100%;
  padding: 1rem;
  margin: 0 auto;
}

.header-content h1 {
  color: #fff;
  margin-top: 0;
  padding-top: 0;
}

.header-content img {
  max-width: 100%;
  width: 380px;
}

.avatar {
  width: 128px;
  height: 128px;
  border-radius: 50%;
}

.box {
  background-color: #2980ba;
  color: white;
  padding: 2rem;
  margin-top: 2rem;
}

.box p {
  font-weight: "bold";
  font-size: 24px;
}

div.range-slider > div.handle:hover > span.tip,
div.range-slider > div.handle:active > span.tip,
div.range-slider > div.handle.dragging > span.tip {
  display: none;
}



